import React, { Component } from 'react';
import {Table,Popconfirm,message} from 'antd'
import {setLocal} from '../../../utils'
import {withRouter} from 'react-router-dom'
import {postAction} from '@/axios'
const curYear = new Date().getFullYear();
class DabiaoTable extends Component {
    state = { 
        data:[],
        pagination: {
            current: 1, // 当前页数，
            pageSize: 10, // 每页条数
            total: 0, // 数据总条数
            showTotal: (total) => `共${total}条数据`, //显示数据总条数
            showQuickJumper: true // 显示页码快速跳转
        },
    }
    componentDidMount() {
        this.getData()
    }
    getData=()=>{
        let param={
            pageInfo:{
                pageNum:this.state.pagination.current,
                pageSize:this.state.pagination.pageSize
            }
        }
        postAction('/bigdata/subsidizeTarget/getSubsidizeTargetList',param).then(res=>{
            if(res.success){
                let list=res.obj.list
                list.map((item,index)=>{
                    item.sortIndex=index+1
                    return item
                })
                this.setState({
                    data:list,
                    pagination: {
                        current: list.pageNum, // 当前页数，
                        pageSize: list.pageSize, // 每页条数
                        total: list.total, // 数据总条数
                        showTotal: (total) => `共${total}条数据`, //显示数据总条数
                        showQuickJumper: true // 显示页码快速跳转
                    },
                })
            }
        })     
    }
    handleEdit=(record)=>{
        setLocal('dabiao',JSON.stringify(record))
        this.props.history.push('/mainframe/systemsetup/dabiaoDetail')
    }
    handleDelete=(record)=>{
        let param={
            "schoolYear":record.schoolYear
        }
        postAction(
            '/bigdata/subsidizeTarget/deleteSubsidizeTarget',
            param
        ).then(res=>{
            if(res.success){
                message.success('删除成功')
                // 更新列表
                this.getData()
            }else{
                message.error('删除失败')
            }
        })
    }
    render() {
        const colums=[
            {
                title: '序号',
                dataIndex: 'sortIndex',
                width:'7%'
            },
            {
                title: '名称',
                dataIndex: 'targetName',
                width:'15%',
                render:(text,record)=>{
                    return (<span title={text}>{text}</span>)
                }
            },
            {
                title: '认定等级：一级',
                dataIndex: 'onelevelTarget',
                render:(text,record)=>{
                    return (<span title={text}>{text}元</span>)
                }
            },
            {
                title: '认定等级：二级',
                dataIndex: 'twolevelTarget',
                render:(text,record)=>{
                    return (<span title={text}>{text}元</span>)
                }
            },
            {
                title: '认定等级：三级',
                dataIndex: 'threelevelTarget',
                render:(text,record)=>{
                    return (<span title={text}>{text}元</span>)
                }
            },
            {
                title: '有效时间',
                dataIndex: 'effectiveDate',
                render:(text,record)=>{
                    return (<span title={text}>{text}</span>)
                }
            },
            {
                title: '更新时间',
                dataIndex: 'updateTime',
                render:(text,record)=>{
                    return (<span title={text}>{text}</span>)
                }
            },
            {
                title: '状态',
                dataIndex: 'status',
                align:'center',
                width:'8%',
                render:(text,record)=>{
                    if(text === '0'){
                        return '过期'
                    }else{
                        return '有效'
                    }
                    
                }
            },
            {
                title: '操作',
                align:'center',
                render:(text,record)=>{
                    if(record.status !== '0'){
                        if(Number(record.schoolYear)>curYear){
                            return (
                                <React.Fragment>
                                    <span style={{color:'#1890ff',cursor:'pointer',marginRight:5}} onClick={()=>{this.handleEdit(record)}}>编辑</span>
                                    <Popconfirm onConfirm={()=>{this.handleDelete(record)}} placement="topRight" title="确定删除该条配置项吗？" okText="确定" cancelText="取消">
                                        <span style={{color:'rgb(141, 153, 176)',cursor:'pointer',marginLeft:5}} >删除</span>
                                    </Popconfirm>
                                    
                                </React.Fragment>
                            )
                        }else{
                            return <span style={{color:'#1890ff',cursor:'pointer'}} onClick={()=>{this.handleEdit(record)}}>编辑</span>
                        }
                        
                    }                  
                }
            },
        ]
        return (
            <Table pagination={this.state.pagination} bordered columns={colums} dataSource={this.state.data}>

            </Table>
        );
    }
}

export default withRouter(DabiaoTable);


