import React, { Component } from 'react';
import {withRouter} from 'react-router-dom'
import {Card,Button} from 'antd'
import DaBiaoTable from './table'
// import {setLocal} from '../../../utils'
class DaBiao extends Component {
    state = {  }
    handleAdd=()=>{
        this.props.history.push('/mainframe/systemsetup/dabiaoDetail')
        // setLocal('dabiao',{})
    }
    render() {
        return (
            <React.Fragment>
                <Card title={<div className='common-title'>资助达标金额配置</div>} extra={<Button onClick={this.handleAdd} icon="plus" type='primary'>添加策略</Button>}>
                    <DaBiaoTable></DaBiaoTable>
                </Card>

            </React.Fragment>
        );
    }
}

export default withRouter(DaBiao);


